<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-left">
        <ctitle :title="'信访矛盾化解攻坚信访量化解率'"></ctitle>
        <div class="detail">
          <div class="w-allCount">
            <p>
              化解
              <incNumber
                class="number yetCount"
                v-if="loadFlag"
                :incNumber="yetcount"
              ></incNumber>
              件次
            </p>
            <p>
              共
              <incNumber
                class="number allCount"
                v-if="loadFlag"
                :incNumber="allcount"
              ></incNumber>
              件次
            </p>
          </div>
          <div class="w-listItem">
            <div
              v-for="(item, index) in depList"
              :key="index"
              class="listItem"
            >
              <label>{{item.name}}</label>
              <div class="listItemRight">
                <p>
                  <incNumber
                    class="number itemCount"
                    v-if="loadFlag"
                    :incNumber="item.jc"
                  ></incNumber>
                  件次
                  <span class="perCount">
                    <incNumber
                      class="number"
                      v-if="loadFlag"
                      :incNumber="item.jcPer"
                    ></incNumber>%
                  </span>
                </p>
              </div>
            </div>
          </div>
        </div>
        <div class="chart">
          <!--<pictorialBar v-if="loadFlag" :params="zlhjlParams" :data="zlhjlData"></pictorialBar>-->
        </div>
      </div>
      <div class="c-right">
        <ctitle :title="'信访矛盾化解攻坚信访量各级化解率'"></ctitle>
        <div class="r-cont">
          <div class="r-top">
            <div
              class="rc-cont"
              v-for="(item,index) in gjhjlData.slice(0,2)"
              :key="index"
            >
              <h4 class="chartTitle">{{item.area}}</h4>
              <div class="echart-wrapper">
                <div
                  class="echart-cont"
                  v-for="(innerItem, innerIndex) in item.data"
                  :key="innerIndex"
                >
                  <!--<petalpie-->
                    <!--v-if="loadFlag"-->
                    <!--:params="gjhjlParams"-->
                    <!--:data="innerItem"-->
                    <!--class="petalpie"-->
                  <!--&gt;</petalpie>-->
                  <p>{{innerItem.name}}</p>
                  <p>{{innerItem.competeCount}}件</p>
                </div>
              </div>
            </div>
          </div>
          <div class="r-bottom">
            <div
              class="rc-cont"
              v-for="(item,index) in gjhjlData.slice(2,4)"
              :key="index"
            >
              <h4 class="chartTitle">{{item.area}}</h4>
              <div class="echart-wrapper">
                <div
                  class="echart-cont"
                  v-for="(innerItem, innerIndex) in item.data"
                  :key="innerIndex"
                >
                  <!--<petalpie-->
                    <!--v-if="loadFlag"-->
                    <!--:params="gjhjlParams"-->
                    <!--:data="innerItem"-->
                    <!--class="petalpie"-->
                  <!--&gt;</petalpie>-->
                  <p>{{innerItem.name}}</p>
                  <p>{{innerItem.competeCount}}件</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import incNumber from "@/components/incNumber/index";
  // import petalpie from "@/components/pie/PetalPie";
  // import pictorialBar from "@/components/pictorialBar/zlhjl";

  export default {
    name: "mdfl",
    components: {
      ctitle,
      incNumber,
      // petalpie,
      // pictorialBar
    },
    props: {
      selectYear: {
        type: Number
      }
    },
    data() {
      return {
        loadFlag: false,
        allcount: 0,
        yetcount: 0,
        depList: [],
        zdzsDetail: [],
        gjhjlData: [],
        gjhjlParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            radius: ["70%", "95%"], // 饼图大小
            color: ["#ffbf7c", "rgba(19,240,255,0.3)"],
            labelColor: "#ffbf7c",
            labelFontSize: "16%"
          }
        },
        zlhjlData: [],
        zlhjlParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            spiritImg: "bg2.png",
            spiritBgImg: "zlhjl-bg.png",
            yAxisLabelSize: 22,
            yAxisLabelShow: true,
            bottom: "10%",
            top: "15%",
            labelColor: "#4eed99",
            labelSize: 16,
            xAxisLabelSize: "16%",
            xAxisLabelColor: "#fefefe",
            categoryBr: true,
            brNum: 4
          }
        }
      };
    },
    mounted() {
      this.getInitApi(this.selectYear);
    },
    methods: {
      getInitApi(year) {
        get_http("mdgjMdfl", {
          year: year
        })
          .then(res => {
            this.data = res.data;
            this.initData(this.data);
            this.loadFlag = true;
          })
          .catch(err => {
            console.log(err);
          });
      },
      initData(data) {
        this.depList = data.xflhjlDetail.data.length
          ? data.xflhjlDetail.data
          : [];
        this.$nextTick(() => {
          let numLength = [];
          this.depList.filter(item => {
            numLength.push(Math.max(item.jc, item.rc).toString().length);
          });
          $(".itemCount").width(Math.max.apply(null, numLength) * 0.2 + "rem");
        });
        this.allcount = data.xflhjlDetail.allcount
          ? data.xflhjlDetail.allcount
          : 0;
        this.yetcount = data.xflhjlDetail.yetcount
          ? data.xflhjlDetail.yetcount
          : 0;
        this.gjhjlData = data.gjhjl.length ? data.gjhjl : [];
        this.zlhjlData = data.zlhjl.length ? data.zlhjl : [];
      }
    },
    watch: {
      selectYear: {
        handler(newVal, oldVal) {
          this.getInitApi(this.selectYear);
        }
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box

      .chartTitle
        position absolute
        color #13f0ff
        font-size 0.18rem
        font-family '微软雅黑'

      .c-left
        flex 2
        display flex
        flex-direction column
        border-right 1px #91e3f7 dashed
        .chart
          width 100%
          flex 1
        .detail
          height 3rem
          padding-top .3rem
          box-sizing border-box
          display flex

          .w-allCount
            width 2.5rem
            text-align center
            box-sizing border-box
            padding-top 0.5rem
            p
              line-height 0.5rem
              text-align right
              &:nth-child(1)
                color #5aceff
              &:nth-child(2)
                color #128f9e
          .w-listItem
            flex 1
            padding-left 0.6rem

            .listItem
              display flex
              line-height .3rem
              margin .2rem 0
              label
                color #fefefe
                font-size 0.16rem
                width 0.67rem
                display inline-block
                margin-right 0.3rem

              .listItemRight
                flex 1
                display flex
                flex-direction column

          p
            font-size 0.16rem
            color #13f0ff

            .yetCount
              display inline-block
              font-size 0.48rem
              color #ffde00
              vertical-align -0.04rem
            .allCount
              display inline-block
              font-size 0.34rem
              color #c4ab00

            .itemCount
              font-size 0.22rem
              color #13fbff
              margin-right 0.1rem
              display inline-block

            .perCount
              display inline-block
              font-size 0.2rem
              color #ffde00
              margin-left 0.2rem

              .number
                display inline-block

      .c-right
        flex 3
        width 100%
        height 100%
        position relative
        .r-cont
          width 90%
          height 80%
          margin 5% auto
          display flex
          flex-direction column
          justify-content center
          align-items center

          .chartTitle
            left 50%
            transform translate(-50%, 0)

          .r-top
            flex 1
            width 100%
            height 100%
            display flex
            border-bottom 0.01rem dashed rgba(71, 232, 243, 0.3)

          .rc-cont
            flex 1
            position relative
            padding-top .3rem
            .echart-wrapper
              display flex
              padding-top 0.6rem

              .echart-cont
                flex 1
                height 100%

                .petalpie
                  width 100%
                  height 100%
                  margin-bottom 0.2rem

                p
                  font-size 0.14rem
                  color #fefefe
                  text-align center
                  line-height 0.2rem

          .rc-cont + .rc-cont
            border-left 0.01rem dashed rgba(71, 232, 243, 0.3)

          .r-bottom
            flex 1
            width 100%
            height 100%
            display flex
</style>
